.photo-img{
    background: aliceblue;
    display: block;
    border-radius: 6px;
}

.photo-img-wrapper{
    border-radius: 6px;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    overflow: hidden;
}

.photo-img-blur{
    -webkit-filter: blur(10px);
            filter: blur(10px);
}

